  // 全選
  setAll2(completed: boolean) {
    console.log(this.source);
    if (this.source == 1) {
      this.district_task.completed = completed;
      if (this.district_task.subtasks == null) {
        return;
      }
      let taskBool = completed
      const taskArray = []
      const idArray = []
      this.district_task.subtasks.map(t => {
        if (t.name == "中和區" ||
          t.name == "新店區" ||
          t.name == "新莊區" ||
          t.name == "八里區" ||
          t.name == "三峽區" ||
          t.name == "坪林區" ||
          t.name == "金山區" ||
          t.name == "淡水區" ||
          t.name == "深坑區" ||
          t.name == "瑞芳區" ||
          t.name == "鶯歌區" ||
          t.name == "三重區" ||
          t.name == "土城區" ||
          t.name == "永和區" ||
          t.name == "汐止區" ||
          t.name == "蘆洲區"
        ) {
          t.completed = completed
          if (completed) {
            taskArray.push(t.name)
            idArray.push(t.value)
          }
        }
      })
      if (taskBool) {
        this.picked_district = idArray
        this.spaceChangeByDistrict(idArray)
        this.expansion2_text = taskArray.toString()
      } else {
        this.picked_district = []
        this.space = [] // 清空
        this.expansion2_text = '空間篩選'
      }
    } else {
      this.district_task.completed = completed;
      if (this.district_task.subtasks == null) {
        return;
      }
      let taskBool = completed
      const taskArray = []
      const idArray = []
      this.district_task.subtasks.map(t => {
        t.completed = completed
        if (completed) {
          taskArray.push(t.name)
          idArray.push(t.value)
        }
      })
      if (taskBool) {
        this.picked_district = idArray
        this.spaceChangeByDistrict(idArray)
        this.expansion2_text = taskArray.toString()
      } else {
        this.picked_district = []
        this.space = [] // 清空
        this.expansion2_text = '空間篩選'
      }
    }

  }
  // 單一更改狀態
  setCompleted2(i: number) {
    this.district_task.subtasks[i].completed = !this.district_task.subtasks[i].completed
    console.log(this.district_task);
    let taskBool = false
    const taskArray = []
    const idArray = []
    this.district_task.subtasks.map(t => {
      if (t.completed) {
        taskBool = true
        taskArray.push(t.name)
        idArray.push(t.value)
      }
    })
    if (taskBool) {
      this.picked_district = idArray
      this.spaceChangeByDistrict(idArray)
      this.expansion2_text = taskArray.toString()
    } else {
      this.picked_district = []
      this.space = [] // 清空
      this.expansion2_text = '空間篩選'
    }
    this.district_task.completed = this.district_task.subtasks != null && taskBool;
  }

  // 當行政區變更時，變更可選項
  spaceChangeByDistrict(idArray) {
    console.log(idArray);
    const a = idArray.map(x => { return this.idSource[x] })
    console.log(a);
    this.space = a.reduce((pre, c) => [...pre, ...c],
      [])

  }

  // 開關id dialog
  openDialogId() {

    console.log(this.space);
    const dialogRef = this.dialog.open(DialogIdComponent, {
      // 避免點擊背景關閉dialog
      disableClose: true,
      width: '60vw',
      maxHeight: '80vh',
      data: {
        source: this.source,
        space: this.space,
      },
    });
    // 關閉id dialog後回傳
    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      this.use_district = true
      result.forEach(e => {
        // console.log(e);
        if (!e.completed) {
          // 如果有id是false 代表不能用行政區查詢
          this.use_district = false
        }
      });
      if (this.use_district) {
        const taskArray = []
        const idArray = []
        this.district_task.subtasks.map(t => {
          if (t.completed) {
            taskArray.push(t.name)
            idArray.push(t.value)
          }
        })
        this.picked_district = idArray
        this.spaceChangeByDistrict(idArray)
        this.expansion2_text = taskArray.toString()
      } else {
        switch (this.source) {
          case 0:
            this.expansion2_text = '空間篩選'
            break;
          case 1:
            this.expansion2_text = '特定街廓'
            break;
          case 2:
            this.expansion2_text = '特定停車場'
            break;
          case 3:
            this.expansion2_text = '特定路段'
            break;
        }
      }
    });

  }
